    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto Slab', sans-serif;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #6E94BC;
}

/*
    Header
*/

header {
    display: grid;
    grid-template-columns: auto 1fr auto;
    
    padding: 20px;
    width: 100%;
    /* justify-content: space-between; */
    position: fixed;
    background-color: rgba(255, 255, 255, 0.3);
    border: solid white 1px;
    align-items: center;
    color: white;
    /* text-decoration: none; */
}

.app-name h1 {
    font-family: "Sail";
    color: #0077B6;
    font-weight: 300;
    font-size: 40px;
}

.nav-web, .nav-user {
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}

ul {
    list-style: none;
    display: flex;
    gap: 30px;
}

a, p {
    text-decoration: none;
    color: white;
    font-size: 25px;
}

h4 {
    color: white;
    font-size: 30px;
}

/*
    Main
*/

main {
    top: 0;
}

/*
    Jumbotron
*/

.jumbotron {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: 100%;
    background-repeat: no-repeat;
}

.jumbotron h1 {
    display: flex;
    justify-content: center;
    padding-block: 14% 26%;
    font-family: "Sail";
    color: #0077B6;
    font-weight: 300;
    font-size: 80px;
}
/*
    Footer
*/

footer {
    display: flex;
    flex-direction: column;
    background-color: #3C4856;
    padding: 5%;
    gap: 2vw;
}

.foo-bar {
    display: grid;
    grid-template-columns: 1fr auto auto;
    gap: 50px;
    justify-content: center;
    align-items: center;
}

.foo-name h1 {
    font-family: "Sail";
    color: #0077B6;
    font-weight: 300;
    font-size: 70px;
}

.foo-web, .foo-bajo {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.credit {
    text-align: end;
}


